:root {
	--pj-bg: #253741;
	--pj-text: #92adbb;
	--pj-highlight: #ffffff;
	--pj-image-size: 800px;
	--pj-image-margin: -450px;
}

.projects {
	margin: 30px 0px;
}

.projects-content {
	background: var(--pj-bg);
	padding: 50px 0px;
	box-sizing: border-box;
	margin-bottom: 30px;
	opacity: 0;
}

.projects-description {
	display: flex;
	align-items: center;
	height: 100%;
	width: 40%;
	padding: 0px 30px;
	box-sizing: border-box;
}

.projects-description h3 {
	font-size: var(--text-size);
	color: var(--pj-highlight);
}

.projects-description button,
.projects-nav button {
	background: none;
	border: none;
	outline: none;
	cursor: pointer;
}

.projects-description button {
	color: var(--pj-text);
	font-size: var(--text-size);
	margin: 0px;
	padding: 0px;
}

.projects-image {
	/* background: orange; */
	max-width: var(--pj-image-size);
	float: right;
	margin-top: var(--pj-image-margin);
}

.projects-image img {
	width: 100%;
}

.projects-nav button {
	font-size: var(--text-size);
	color: var(--text-color);
	margin-right: 10px;
}

.projects-nav button:hover {
	color: white;
}

.show {
	opacity: 1;
	transition: opacity 1.5s ease-in;
}

@media only screen and (max-width: 1380px) {
	:root {
		--pj-image-size: 700px;
		--pj-image-margin: -400px;
	}
}

@media only screen and (max-width: 1280px) {
	:root {
		--pj-image-size: 600px;
		--pj-image-margin: -350px;
	}
}

@media only screen and (max-width: 1050px) {
	:root {
		--pj-image-size: 500px;
		--pj-image-margin: -325px;
	}
}

@media only screen and (max-width: 1050px) {
	.projects-content {
		display: flex;
		justify-content: center;
		align-items: center;
		padding: 0px;
	}
	.projects-image {
		float: none;
		margin: 0px;
	}
}

@media only screen and (max-width: 850px) {
	.projects-image {
		float: none;
		margin: 0px;
		max-width: 90%;
	}

	.projects-description {
		width: 100%;
		padding: 30px;
		box-sizing: border-box;
	}

	.projects-content {
		padding: 0px;
		flex-direction: column;
	}

	.projects-nav {
		margin: auto;
		width: fit-content;
	}
}

@media only screen and (max-width: 600px) {
	.projects-image {
		max-width: 100%;
	}
}
